'use client'

import { useQuery } from '@tanstack/react-query'
import { Card, Button, Dialog, TextField } from 'frosted-ui'
import { Users, Dumbbell, CheckCircle, UserPlus, Plus, UserCheck, Eye, Settings } from 'lucide-react'
import Link from 'next/link'
import { useState } from 'react'
import { useWhop } from '~/components/whop-context'
import { dashboardStatsQuery, createPlanMutation } from '~/components/workouts/queries'
import { useMutation, useQueryClient } from '@tanstack/react-query'

export default function DashboardPage() {
  const { access, experience } = useWhop()
  const isAdmin = access.accessLevel === 'owner' || access.accessLevel === 'admin'
  
  if (!isAdmin) {
    return (
      <div className="p-6">
        <div className="text-sm opacity-70">You don't have access to this page.</div>
      </div>
    )
  }

  const { data: stats, isLoading, error } = useQuery(dashboardStatsQuery(experience.id))

  const metrics = [
    {
      icon: Users,
      label: 'Total Clients',
      value: stats?.totalClients ?? 0,
      color: 'text-blue-600',
      bgColor: 'bg-blue-50 dark:bg-blue-950'
    },
    {
      icon: Dumbbell,
      label: 'Workout Plans',
      value: stats?.totalPlans ?? 0,
      color: 'text-green-600',
      bgColor: 'bg-green-50 dark:bg-green-950'
    },
    {
      icon: CheckCircle,
      label: 'Total Assignments',
      value: stats?.totalAssignments ?? 0,
      color: 'text-purple-600',
      bgColor: 'bg-purple-50 dark:bg-purple-950'
    },
    {
      icon: UserPlus,
      label: 'Recent Assignments',
      value: stats?.recentAssignments ?? 0,
      color: 'text-orange-600',
      bgColor: 'bg-orange-50 dark:bg-orange-950'
    }
  ]

  return (
    <div className="p-6">
      <div className="mb-6">
        <h1 className="text-2xl font-bold">Dashboard</h1>
      </div>

      {/* Clean Metrics Grid */}
      <div className="grid grid-cols-2 lg:grid-cols-4 gap-4 mb-6">
        {metrics.map((metric, index) => {
          const Icon = metric.icon
          return (
            <Card key={index}>
              <div className="p-4">
                <div className="flex justify-between items-start mb-2">
                  <p className="text-sm opacity-70">{metric.label}</p>
                  <Icon className={`w-4 h-4 ${metric.color}`} />
                </div>
                {isLoading ? (
                  <div className="h-6 w-12 bg-gray-200 dark:bg-gray-700 rounded animate-pulse"></div>
                ) : error ? (
                  <p className="text-sm text-red-600">Error</p>
                ) : (
                  <p className="text-2xl font-bold">{metric.value}</p>
                )}
              </div>
            </Card>
          )
        })}
      </div>

      {/* Simple Content */}
      <div className="grid gap-4 grid-cols-1 md:grid-cols-2">
        <Card><div className="p-4">Quick Actions</div></Card>
        <Card><div className="p-4">Recent Activity</div></Card>
      </div>
    </div>
  )
}


